<template>
  <header class="section-header">
    <div class="section-header__wrap">
      <div class="section-header__logo">
        <img src="@/assets/images/header/logo.png"/>
      </div>
      <div class="section-header__menus">
        <div
          v-for="(item, index) in headerMenus"
          :key="index"
          class="menu"
          :class="{ active : item.active }"
        >
          <a @click="handleChangeActive(index)">{{ item.name }}</a>
        </div>
      </div>
      <div class="section-header__info">
        <button class="sign-up info">Join Now</button>
        <button class="sign-in info">Login</button>
      </div>
    </div>
    <div class="section-header__banner">
      <img src="@/assets/images/header/header-banner.png"/>
    </div>
  </header>
</template>
<script>
import { headerMenus } from '@/resources/header'
export default {
  data() {
    return {
      headerMenus
    }
  },
  methods: {
    handleChangeActive (index) {
      this.headerMenus.forEach((item, i) => {
        item.active = index === i ? true : false;
      })
    }
  }
}
</script>
