<template>
  <div class="tab-normal-pick">
    <div
        class="tab-normal-pick__previous icon-common"
        :class="{ active : isActivePrevious }"
    >
      <img src="@/assets/images/loto/previous.png"/>
    </div>
    <div
        class="tab-normal-pick__next icon-common"
        :class="{ active : isActiveNext }"
    >
      <img src="@/assets/images/loto/next.png"/>
    </div>
    <div class="tab-normal-pick__container">
      <div class="text-notify">
        Select 7 numbers from 1 to 28 or select Quick Pick.
      </div>
      <div class="tab-normal-pick__loto-container">
        <div
            v-for="(item, index) in lotoNumbers"
            :key="index"
        >
          <div class="table-loto">
            <div class="table-loto__title">Select your 7 number</div>
            <div class="table-loto__wrap">
              <div class="table-loto__container">
                <div
                    v-for="(loto, i) in lotoNumbers[index]"
                    :key="i"
                    class="table-loto__item"
                    @click="handlePickItem(index, i)"
                >
                  <img
                      v-if="loto.active"
                      src="@/assets/images/loto/selection-tick.png"
                  />
                  <a>{{ loto.number }}</a>
                </div>
              </div>
              <div class="table-loto__submit">
                <button @click="handleQuickPick(index)">Quick Pick</button>
                <div class="icon" @click="handleDeleteTable(index)">
                  <img src="@/assets/images/loto/delete.png"/>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="tab-normal-pick__add-ticket">
        <button @click="handleAddTicket">+ Add another Ticket</button>
      </div>
    </div>
  </div>
</template>

<script>
import { lotoNumbers } from '@/resources/loto'

export default {
  data() {
    return {
      lotoNumbers,
      isActiveNext: true,
      isActivePrevious: false
    }
  },
  methods: {
    handleQuickPick(index) {
      const numbers = Array(28).fill().map((_, index) => index + 1);
      numbers.sort(() => Math.random() - 0.5);
      const numbersRandom = numbers.slice(0, 7);
      this.lotoNumbers.forEach((item, i) => {
        if (index === i) {
          item.forEach(item => item.active = numbersRandom.includes(item.number) ? true : false)
        }
      })
    },
    handleDeleteTable(index) {
      this.$bvModal.msgBoxConfirm('Are you sure to delete?', {title: 'Please Confirm'})
          .then(value => {
            value ? this.lotoNumbers = this.lotoNumbers.filter((_, i) => i !== index) : null
          })
          .catch(err => {
            console.log(err);
          })
    },
    handlePickItem(indexList, indexNumber) {
      this.lotoNumbers.forEach((item, i) => {
        if (indexList === i) {
          const itemActive = item.filter(item => item.active === true);
          item.forEach((value, index) => {
            if (itemActive.length < 7 || value.active) {
              if (index === indexNumber) {
                value.active = !value.active;
              }
            }
          })
        }
      })
    },
    handleAddTicket() {
      this.lotoNumbers = [...this.lotoNumbers, [
        {
          active: false,
          number: 1,
        },
        {
          active: false,
          number: 2,
        },
        {
          active: false,
          number: 3,
        },
        {
          active: false,
          number: 4,
        },
        {
          active: false,
          number: 5,
        },
        {
          active: false,
          number: 6,
        },
        {
          active: false,
          number: 7,
        },
        {
          active: false,
          number: 8,
        },
        {
          active: false,
          number: 9,
        },
        {
          active: false,
          number: 10,
        },
        {
          active: false,
          number: 11,
        },
        {
          active: false,
          number: 12,
        },
        {
          active: false,
          number: 13,
        },
        {
          active: false,
          number: 14,
        },
        {
          active: false,
          number: 15,
        },
        {
          active: false,
          number: 16,
        },
        {
          active: false,
          number: 17,
        },
        {
          active: false,
          number: 18,
        },
        {
          active: false,
          number: 19,
        },
        {
          active: false,
          number: 20,
        },
        {
          active: false,
          number: 21,
        },
        {
          active: false,
          number: 22,
        },
        {
          active: false,
          number: 23,
        },
        {
          active: false,
          number: 24,
        },
        {
          active: false,
          number: 25,
        },
        {
          active: false,
          number: 26,
        },
        {
          active: false,
          number: 27,
        },
        {
          active: false,
          number: 28,
        }
      ]]
    }
  }
}
</script>

